import React from 'react';
import styled from 'styled-components';

const Wrapper = styled.div`
  text-align: left;
`;

const Title = styled.div`
  font-size: 12px;
  line-height: 20px;
  color: rgba(238,242,248,1);
`;

const Description = styled.div`
  font-size: 12px;
  line-height: 20px;
  color: rgba(255,255,255,0.56);
`;

export interface IIconButtonShortcutProps {
  title: string;
  shortcut?: string;
  description?: string;
}

export const IconButtonShortcut: React.FC<IIconButtonShortcutProps> = (props) => {
  const { title, shortcut, description } = props;
  return (
    <Wrapper>
      <Title>{`${title}${shortcut ? '(' + shortcut + ')' : ''}`}</Title>
      {
        description && <Description>{description}</Description>
      }
    </Wrapper>
  );
}
